import React, { useEffect, useState } from 'react'
import Form from '../SharedComponents/Form'

import { useParams } from 'react-router-dom'
import axios from 'axios'

export default function UpdateWorkshop() {
    const params = useParams();
    const [data, setData] = useState();

    useEffect(() => {
        async function getSingleWorkshop() {
            try {
                const { data } = await axios.get(`http://localhost:5000/workshop/${params.id}`)
                setData(data)
            } catch (error) {
                console.log(error);
            }
        }
        getSingleWorkshop();
    },[params.id])

    return (
        <>
            <h1 className='font-bold text-xl my-4 text-gray-400'>Edit Workshop - {data && <span className='lowercase first-letter:uppercase'>{data.workshop.name}</span>}</h1>
            <Form
                method="patch"
                url={`http://localhost:5000/workshop/${params.id}`} />
        </>
    )
}
