import React from 'react'

export default function Input(props) {
    return (
        <div className='flex flex-col mb-4'>
            <label htmlFor={props.id} className='text-lg text-red-500 font-semibold'>{props.title}</label>

            <input type={props.id === "date" ? "datetime-local" : "text"}
                {...props}
                className='border-b border-b-black p-1 outline-none focus:border-b-2 placeholder:text-gray-400 focus:placeholder:text-gray-300'
            />
        </div>
    )
}
