import React from 'react'
import { BsCalendar3 } from 'react-icons/bs'
import { BiWifi } from 'react-icons/bi'
import { IoEarthSharp } from 'react-icons/io5'
import { HiPencil } from 'react-icons/hi'
import { Link } from 'react-router-dom'
import momentjs from 'moment';



export default function Card(props) {
    return (
        <div className='w-[300px] pb-2 bg-gray-300 rounded-lg overflow-hidden'>
            <div className='min--[150px]'>
                <img src="https://picsum.photos/seed/picsum/800/200" alt="" className='min-w-full min-h-[150px]' />
            </div>
            <div className="p-4">
                <div className='flex justify-between mb-4'>
                    <h1 className='text-xl font-semibold lowercase first-letter:uppercase'>{props.title}</h1>
                    <p><span className=' rounded-lg bg-blue-500 text-blue-900 px-2 py-0 text-center text-sm'>{props.type.toUpperCase()}</span></p>
                </div>
                <div className='flex flex-col gap-4'>
                    <p className='flex gap-2 items-center'><BsCalendar3 />  <span className='font-semibold'>{momentjs(props.date).format("Do MMM YYYY, hh:mm a")}</span>
                    </p>

                    <p className='flex gap-2 items-center'><IoEarthSharp />  <span className='font-semibold lowercase first-letter:uppercase'>{props.venue}</span>
                    </p>

                    {
                        props.link ?
                            <a className='flex gap-2 items-center overflow-hidden' href={`${props.link}`}><span><BiWifi /></span><span className='text-blue-700 hover:underline '>{props.link}</span>
                            </a> : <p>&nbsp;</p>
                    }
                </div>
                <div>
                    <Link to={`/update/${props.id}`} className='float-right my-2'><HiPencil /></Link>
                </div>
            </div>
        </div>
    )
}
