import axios from 'axios';
import React, { useEffect, useState } from 'react'
import Card from './Card'

export default function DisplayWorkshop() {
  const [data, setData] = useState();
  const [error, setError] = useState();
  useEffect(() => {
    async function getWorkshops() {
      try {
        const { data } = await axios.get("http://localhost:5000/workshop");
        setData(data)
      } catch (error) {
        setError(error)
        console.log(error);
      }
    }
    getWorkshops();
  }, [])
  if (error) {
    return (<>
      <h1>Someting went wrong...</h1>
      <p>{JSON.stringify(error.message)}</p>
    </>
    )
  }

  return (
    <>
      <div className='min-w-[100%]'>
        <div className='grid grid-cols-1 sm:grid-cols-2 place-items-center gap-8 p-8'>
          {
            data &&
            data.workshops.map(workshop => {
              return (
                <Card
                  key={workshop._id}
                  id={workshop._id}
                  title={workshop.name}
                  type={workshop.type}
                  date={workshop.date}
                  venue={workshop.venue}
                  link={workshop.link} />
              )
            })
          }
        </div>
      </div>
    </>
  )
}
