import React, { useState } from 'react'
import { Link } from 'react-router-dom'

function Li(props){
  return (
    <li className='hover:bg-gray-200 py-4  font-bold text-gray-500 bg-gray-100'>{props.children}</li>
  )
}
export default function SideNavbar() {
  const [click,setClick] = useState(1);
  return (
    <nav className='min-w-max py-8'>
      <ul className='flex flex-col gap-4'>
       <Li><Link to="/" className={`p-4  ${click === 1?"border-l-8 border-l-red-700 text-red-700":""}` } onClick={()=>{setClick(1)}}>Display Workshop</Link></Li>
       <Li><Link to="/create" className={`p-4  ${click === 2?"border-l-8 border-l-red-700 text-red-700":""}`} onClick={()=>{setClick(2)}}>Create Workshop  </Link></Li>
      </ul>
    </nav>
  )
}
