import axios from 'axios';
import React, { useState } from 'react'
import Input from './Input';
import { BsImage } from 'react-icons/bs'


export default function Form({ method, url }) {
    const [input, setInput] = useState({
        name: "",
        venue: "",
        type: "",
        link: "",
        date: ""
    })


    function handleInput(e) {
        const { value, name } = e.target;
        setInput((prev) => {
            return {
                ...prev,
                [name]: value
            }
        })
    }

    function handleSubmit(e) {
        e.preventDefault();
        console.log(input)
        async function sendData() {
            try {
                await axios({
                    method: method,
                    url: url,
                    data: input
                })

                setInput({
                    name: "",
                    venue: "",
                    type: "",
                    link: "",
                    date: ""
                })
            } catch (error) {
                console.log(error);
            }
        }
        sendData();
    }
    return (
        <form className='flex flex-col gap-4' onSubmit={handleSubmit}>
            <div className="flex justify-center items-center relative">
                <img src="https://picsum.photos/seed/picsum/800/200" alt="" className="rounded-lg min-w-full" />
                <button type='button' className=" px-2 py-1 rounded-lg absolute bottom-2 right-4 flex gap-1 bg-white text-black justify-center items-center"><BsImage /><span>Change Cover Image</span></button>
            </div>


            <div className='grid grid-cols-2 gap-16 p-8'>
                <Input
                    title="Workshop name"
                    id="name"
                    name="name"
                    placeholder="Talk show..."
                    value={input.name}
                    onChange={handleInput}
                    required={true}
                />
                <Input
                    title="Workshop Venue"
                    id="venue"
                    name="venue"
                    placeholder="Delhi, India"
                    value={input.venue}
                    onChange={handleInput}
                    required={true}
                />
                <Input
                    title="Workshop Type"
                    id="type"
                    name="type"
                    placeholder="Online/In Person"
                    value={input.type}
                    onChange={handleInput}
                    required={true}
                />
                {
                    input.type.toLowerCase() === "online" &&
                    <Input
                        title="Workshop URL"
                        id="link"
                        name="link"
                        placeholder="zoom / google meet url"
                        value={input.link}
                        onChange={handleInput}
                    />
                }
                <Input
                    title="Workshop Date"
                    id="date"
                    name="date"
                    placeholder="dd/mm/yyyy hh:mm:ss"
                    value={input.date}
                    onChange={handleInput}
                    required={true}
                />
            </div>
            <div className='flex justify-center items-center'>
                <button className='rounded-lg text-white font-semibold bg-red-700 mix-w-min px-2 py-1'>{method === "post" ? "Create Workshop" : "Update Workshop"}</button>
            </div>
        </form>
    )
}
